import logo from './dragon.png';
import { Component } from 'react';
import { CardList } from './components/card-list/card-list.component';
import { SearchBox } from './components/search-box/search-box.component';
import './App.css';

class App extends Component {
  constructor() {
    super();

    this.state = {
      users: [],
      searchInput: ""
    }
  }

  componentDidMount() {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then(response => response.json())
      .then(users => this.setState({ users: users }))
  }

  render() {
    const { users, searchInput } = this.state;
    const filteredUsers = users.filter(user => user.name.toLowerCase().includes(searchInput.toLowerCase()));

    return (
      <div className="App" >
        <h1> Duppah's Org </h1>
        <img src={logo} className="App-logo" alt="logo" />
        <br />
        <SearchBox
          placeholder="Search Users"
          handleChange={e => this.setState({ searchInput: e.target.value })} />
        <CardList users={filteredUsers} />
      </div>
    )
  }
}

export default App;
